body {
  background-image: url("header_img.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-color: black;
}
@media only screen and (max-width: 600px) {
  body {
    background-image: url("header_img_m.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-attachment: fixed;
  }
  .centered {
    padding: 20% 10% 5% 10%;
  }
}
.text-custom {
  animation: change_color 30s 2s infinite;
  animation-direction: alternate;
}

@keyframes fade {
  from {
    opacity: 0;
    margin-top: 100%;
  }
  to {
    opacity: 1;
    margin-top: 0;
  }
}
@keyframes fade_h {
  from {
    opacity: 0;
    margin-left: 100%;
  }
  to {
    opacity: 1;
    margin-left: 0;
  }
}
@keyframes fade_sbar {
  from {
    opacity: 0;
    margin-left: 0;
  }
  to {
    opacity: 1;
    margin-left: 70%;
  }
}
@keyframes change_color {
  0% {
    color: rgb(177, 0, 44);
  }
  25% {
    color: rgb(189, 69, 0);
  }
  50% {
    color: rgb(20, 172, 0);
  }
  75% {
    color: rgb(2, 81, 201);
  }
  100% {
    color: #9300b8;
  }
}
@keyframes fade_img {
  from {
    opacity: 0;
    margin-left: 100%;
  }
  to {
    opacity: 1;
    margin-left: 0;
  }
}
